export type BaseLogParams = {
  eventName: ValueOf<EventName>;
  eventNameHint?: string;
  uiName?: ValueOf<UiName>;
  creativeName?: string;
  index?: number;
  items?: Record<string, any>[];
  itemId?: string | number;
  itemName?: string;
  itemType?: string;
  sortOption?: unknown;
  topicId?: number;
  categoryId?: number;
};
export const sendEvent = <T = unknown>({
  items,
  ...restParams
}: BaseLogParams & T) => {
  const primitivesOnlyParams = Object.entries(restParams).reduce<
    Record<string, Primitive>
  >((acc, [key, value]) => {
    const isPrimitive = !(value !== null && typeof value === 'object');
    return {
      ...acc,
      [key]: isPrimitive ? value : JSON.stringify(value),
    };
  }, {});
  const parsedEventParams = decamelizeKeys({
    ...primitivesOnlyParams,
    items,
  });

  NativeCommunicator.callToNative('sendEvent', {
    data: parsedEventParams,
  });
};

export const WEBVIEW_PAGE_LAYOUT = {
  /** @description    */
  PAGE: 0,
  /** @description  X  */
  POP_UP: 1,
} as const;
interface OpenNewWebviewParams {
  url: string;
  title: string;
  layout?: ValueOf<typeof WEBVIEW_PAGE_LAYOUT>;
  screen_name?: string;
}
export const openNewWebview = ({
  url,
  title,
  layout = WEBVIEW_PAGE_LAYOUT.PAGE,
  screen_name,
}: OpenNewWebviewParams) => {
  NativeCommunicator.callToNative('openWebview', {
    data: {
      title,
      url,
      mode: layout,
      screen_name,
    },
  });
};

