import { Global, ThemeProvider } from '@emotion/react';
import type { AppProps } from 'next/app';
import Head from 'next/head';
import { useEffect } from 'react';

import globalStyles from 'src/styles/globalStyles';
import theme from 'src/styles/theme';
import { APM, initializeAPM } from 'src/utils/apm';
import { NativeCommunicator } from 'src/utils/nativeCommunicator';
import ReactQueryClientProvider from 'src/utils/ReactQueryClientProvider';

if (typeof window !== 'undefined') {
  initializeAPM();

  const pageName = window.location.pathname;
  APM.setInitialPageLoadName(pageName);
}

const MyApp = ({ Component, pageProps }: AppProps): JSX.Element => {
  useEffect(() => {
    NativeCommunicator.initWebviewCallbacks();
  }, []);
  return (
    <>
      <Head>
        <meta
          name="viewport"
          content="width=device-width, initial-scale=1, maximum-scale=1.0, minimum-scale=1, user-scalable=0, viewport-fit=cover"
        />
      </Head>

      <ReactQueryClientProvider>
        <ThemeProvider theme={theme}>
          <Global styles={globalStyles} />
          <Component {...pageProps} />
        </ThemeProvider>
      </ReactQueryClientProvider>
    </>
  );
};

export default MyApp;
