import type { Maybe } from 'src/types/util';

import type { HwahaeNativeInterfaces as NativeInterfaces } from './hwahaeNativeInterface';

type Callback<T> = (params?: T) => void;

interface CallbackObject<SuccessParam = undefined, FailParam = undefined> {
  callbackId: string;
  onSuccess?: Callback<SuccessParam>;
  onFail?: Callback<FailParam>;
}

type CallToNativeCallbacks<
  SuccessParam = undefined,
  FailParam = undefined,
> = Partial<CallbackObject<SuccessParam, FailParam>>;

class NativeCommunicator {
  private static IS_CLIENT_SIDE = typeof window !== 'undefined';

  static initWebviewCallbacks() {
    if (this.IS_CLIENT_SIDE) {
      window.hwahaeCallbacks = {};
    }
  }

  private static run<T, U = T>(
    input: Maybe<U>,
    transform: (_: U) => Maybe<T>,
  ): Maybe<T> {
    if (typeof input === 'undefined') {
      return;
    }
    return transform(input);
  }

  private static cleanupCallback = (callbackId: string) => {
    if (window.hwahaeCallbacks?.[callbackId]) {
      delete window.hwahaeCallbacks[callbackId];
    }
  };

  private static runCallback<T = undefined>({
    jsonText,
    callback,
    callbackId,
  }: {
    jsonText: Maybe<string>;
    callbackId: string;
    callback: Maybe<Callback<T>>;
  }): void {
    try {
      const params = this.run(jsonText, JSON.parse);
      callback?.(params);
    } catch (e) {
      callback?.();
    } finally {
      this.cleanupCallback(callbackId);
    }
  }

  private static registerCallbacks({
    callbackId,
    onSuccess,
    onFail,
  }: CallbackObject): void {
    if (this.IS_CLIENT_SIDE) {
      window.hwahaeCallbacks = {
        ...window.hwahaeCallbacks,
        [callbackId]: {
          success: (jsonText) =>
            this.runCallback({ jsonText, callback: onSuccess, callbackId }),
          fail: (jsonText) =>
            this.runCallback({ jsonText, callback: onFail, callbackId }),
        },
      };
    }
  }

  static makeCurriedCallToNative<
    SuccessParam = undefined,
    FailParam = undefined,
    ExpansionData = Record<string, unknown>,
    InterfaceKey extends keyof NativeInterfaces = keyof NativeInterfaces,
  >(interfaceName: InterfaceKey) {
    return (
      option: Maybe<{
        data?: NativeInterfaces[InterfaceKey] & ExpansionData;
        callbacks?: CallToNativeCallbacks<
          Maybe<SuccessParam>,
          Maybe<FailParam>
        >;
      }> = {},
    ) => {
      NativeCommunicator.callToNative(interfaceName, option);
    };
  }

  static callToNative<
    SuccessParam = undefined,
    FailParam = undefined,
    ExpansionData = Record<string, unknown>,
    InterfaceKey extends keyof NativeInterfaces = keyof NativeInterfaces,
  >(
    interfaceName: InterfaceKey,
    option: Maybe<{
      data?: NativeInterfaces[InterfaceKey] & ExpansionData;
      callbacks?: CallToNativeCallbacks<Maybe<SuccessParam>, Maybe<FailParam>>;
    }> = {},
  ): void {
    if (!this.IS_CLIENT_SIDE) {
      return;
    }

    const { callbacks, data = {} } = option;
    const callbackId = `${interfaceName}_${new Date().getTime()}`;

    this.run(
      {
        ...callbacks,
        callbackId,
      },
      this.registerCallbacks.bind(this),
    );

    const requestParam = JSON.stringify({
      ...(callbacks && {
        callbackId,
      }),
      ...data,
    });
    if (window.hwahaeInterface) {
      // Android
      window.hwahaeInterface[interfaceName](requestParam);
    } else if (window.webkit?.messageHandlers) {
      // iOS
      window.webkit.messageHandlers[interfaceName].postMessage(requestParam);
    } else {
      console.group('environment is not native');
      console.log('interface name is: ', interfaceName);
      console.log('requestParam: ', JSON.parse(requestParam));
      console.log('callbacks: ', callbacks);
      console.groupEnd();
    }
  }
}

export default NativeCommunicator;
